:local(.container) label {
  display: block;
  font-weight: bolder;
  font-size: 14px;
  padding: 4px 0px; }

:local(.span) {
  line-height: 40px;
  text-decoration: none; }
  :local(.span):visited {
    color: inherit; }
  :local(.span) > span.fa {
    margin-right: 8px;
    color: #8c969f; }



