:local(.icon) {
  display: inline-block;
  background: -webkit-linear-gradient(315deg, #cacaca, #fbfbfb);
  background: -o-linear-gradient(315deg, #cacaca, #fbfbfb);
  background: linear-gradient(135deg, #cacaca, #fbfbfb);
  border-radius: 50%;
  color: #778da7;
  padding: 8px;
  font-size: 12px;
  width: 30px;
  height: 30px;
  margin: 6px;
  text-align: center; }
  :local(.icon).button {
    cursor: pointer;
    background: #fff;
    padding: 6px;
    -webkit-box-shadow: 0 1px 2px 0 #afafc5;
            box-shadow: 0 1px 2px 0 #afafc5;
    height: 42px;
    width: 42px;
    font-size: 24px;
    -webkit-transition: 150ms;
    -o-transition: 150ms;
    transition: 150ms; }
    :local(.icon).button:hover {
      background-color: #eee; }
  :local(.icon).edit {
    color: #ff7d38; }
  :local(.icon).cancel {
    color: #ef4836; }
  :local(.icon).save {
    color: #0392f5; }
  :local(.icon).small {
    height: 30px;
    width: 30px;
    font-size: 16px; }



